___CSS_LOADER_EXPORT___.push([module.id, `.card-wrapper {
  position: relative;
  cursor: pointer;
}

.cards {
  display: flex;
  gap: 4rem;
}

.card {
  width: 300px;
  height: 180px;
  padding: 2rem;
  background: #333;
  color: #fff;
  border-radius: 10px;
  transition: transform .4s;
  position: relative;
  z-index: 2;
}

.card-detail {
  position: absolute;
  top: 0;
  left: 260px;
  width: 320px;
  height: 100%;
  padding: 2rem;
  background: #111;
  color: #fff;
  border-radius: 10px;

  opacity: 0;
  transform: translateX(40px);
  pointer-events: none;
  transition: .4s;
}

.card-wrapper.active .card {
  transform: scale(1.15);
}

.card-wrapper.active .card-detail {
  opacity: 1;
  transform: translateX(0);
}
